.faq{margin-top:50px}.faq-header{display:flex;justify-content:space-between}.faq_block_first{border:1px solid #575757;border-radius:10px;padding:15px 20px;user-select:none;margin-bottom:20px;transition:max-height 1s ease,opacity 1s ease}.faq_block-text{color:#cfcfcf;display:block}.faq-content{max-height:0;opacity:0}.faq-content-show{max-height:1000px;opacity:1;padding:10px 0;transition:max-height 1s ease,opacity 1s ease;color:#575757}.icon{width:20px;height:20px}